/* src/components/FlashcardGame.css */
.flashcard-game {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px; /* Add some padding for spacing */
}

.button-container {
  display: flex;
  justify-content: center; /* Center the buttons horizontally */
  align-items: center; /* Center the buttons vertically */
  margin-top: 10px; /* Add some margin for spacing */
}

.back-button,
.next-button {
  padding: 10px 20px;
  font-size: 14px; /* Adjust font size for smaller screens */
  border: none;
  background-color: #007bff;
  color: #fff;
  cursor: pointer;
  margin: 0 10px; /* Add spacing between buttons */
}


.progress-bar {
  width: 100%;
  height: 20px; /* Reduce progress bar height for smaller screens */
  background-color: #ccc;
  border-radius: 2.5px; /* Adjust border radius */
  margin: 5px 0; /* Add some margin for spacing */
  position: relative;
}

.progress-fill {
  height: 100%;
  border-radius: 2.5px; /* Adjust border radius */
  background-color: #007bff;
  transition: width 0.3s;
}

.progress-bar-container {
  width: 100%;
  max-width: 400px; /* Limit the width to avoid stretching */
}

.footer {
  position: fixed;
  bottom: 0;
  width: 100%;
  background-color: #cfe8ef;
  padding: 10px 0;
  text-align: center;
}

.share-footer-content {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.attribution {
  margin-top: 10px; /* Add some space between share links and attribution */
  color: #777;
}

/* Style for the header */
.MuiAppBar-root {
  background-color: #007bff;
  margin-bottom: 2%;
}

/* Style for the title in the header */
.MuiTypography-h6 {
  font-weight: bold;
  text-align: center;
}

/* Style for the menu items */
.MuiMenuItem-root {
  font-size: 16px;
}

@media (max-width: 600px) {
  .back-button,
  .next-button {
    font-size: 12px; /* Adjust font size for smaller screens */
  }
}